import Vue from 'vue'
import VueRouter from 'vue-router'
import SignIn from '../views/LetsIn/SignIn/Index.vue'
import SignUp from '../views/LetsIn/SignUp/Index.vue'
import LetsIn from '../views/LetsIn/Lets_In/Index.vue'
import FillProfile from '../views/Account_setup/Fill_Profile/index.vue'
import NewPin from '../views/Account_setup/New_PIN/index.vue'
import ForgotPassword from '../views/Forgot_resetPassword/Forgot_password/index.vue'
import VerifyPassword from '../views/Forgot_resetPassword/Forgot_password/verify_password.vue'
import NewPassword from '../views/Forgot_resetPassword/Reset_password/index.vue'

Vue.use(VueRouter)

const routes = [
  {
    path : '/',
    redirect: 'fill-profile'
  },
  {
    path: '/lets-in',
    name: 'lets-in',
    component: LetsIn
  },
  {
    path: '/sign-in',
    name: 'sign-in',
    component: SignIn
  },
  {
    path: '/sign-up',
    name: 'sign-up',
    component: SignUp,
    // children: [
    //   {
    //     path: '/fill-profile',
    //     name: 'fill-profile',
    //     component: FillProfile
    //   }
    // ]
  },
  {
    path: '/fill-profile',
    name: 'fill-profile',
    component: FillProfile
  },
  {
    path: '/new-pin',
    name: 'new-pin',
    component: NewPin
  },
  {
    path: '/forgot-password',
    name: '/forgot-password',
    component: ForgotPassword
  },
  {
    path: '/verify-password',
    name: '/verify-password',
    component: VerifyPassword
  },
  {
    path: '/new-password',
    name: '/new-password',
    component: NewPassword
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
