<template>
  <div class="form">
    <backButtonVue :value="'Creat New Password'"></backButtonVue>
    <img class="form__img" src="../../../assets/new-pass.jpg" alt="">
    <p>Create Your New Password</p>
    <inputVue 
      :value="password" 
      class="input-vue" 
      :placeholder="'Password'" 
      :type="'password'"
      @keyPress="password = $event"
    />
    <inputVue 
      :value="confirmPassword" 
      class="input-vue" 
      :placeholder="'Confirm password'" 
      :type="'password'"
      @keyPress="confirmPassword = $event"
    />
    <rememberMe class="checkbox" @onChecked="rememberMe = $event"></rememberMe>
    <buttonVue :value="'Continue'" @onClick="onClick($event)" />
    <congratsVue v-if="show"></congratsVue>
  </div>
</template>

<script>
import backButtonVue from "@/components/base/backButton.vue";
import inputVue from "@/components/base/input.vue"
import rememberMe from "@/components/base/remember.vue";
import buttonVue from "@/components/base/button.vue";
import congratsVue from "@/components/Popup/congrats.vue";

export default {
  components: {
    backButtonVue,
    inputVue,
    rememberMe,
    buttonVue,
    congratsVue
  },
  data() {
    return {
      show: false,
      password: '',
      confirmPassword: ''
    }
  },
  methods: {
    onClick: function () {
      console.log(this.password);
      console.log(this.confirmPassword);
      if (this.password == "" || this.confirmPassword == "") {
        alert("Vui long nhap");
        return;
      }
      if (this.password !== this.confirmPassword) {
        alert("Nhập sai");
        return;
      }

      this.show = !this.show;
    },
  },
}
</script>

<style lang="scss" scoped>
.form {
  display: flex;
  flex-direction: column;
  margin: 0;
  width: 90%;
  margin: auto;
  letter-spacing: normal;

  &__img {
    width: 80%;
    margin-top: 85px;
    margin-left: auto;
    margin-right: auto;
  }

  p {
    margin-top: 50px;
    margin-bottom: 20px;
    font-size: 17px;
    font-family: monospace;
    font-weight: 400;
  }

  .input-vue {
    margin-bottom: 20px;
  }

  .checkbox {
    margin-top: 5px;
    margin-bottom: 50px;
  }
}
</style>